import React from 'react';

class Register extends React.Component{
  constructor(props){
    super(props);
    this.state={
      userEmail:'',
      userPassword:'',
      userName:''
    }
  }

onEmailChange=(event)=>{
this.setState({userEmail:event.target.value});
}

onPasswordChange=(event)=>{
this.setState({userPassword:event.target.value});
}

onNameChange=(event)=>{
this.setState({userName:event.target.value});
}

onRegister=()=>{
fetch('https://protected-shelf-62570.herokuapp.com/register', {
  method:'POST',
  headers:{'content-type':'application/json'},
  body:JSON.stringify({
    email:this.state.userEmail,
    password:this.state.userPassword,
    name:this.state.userName
  })
}).then(response=>response.json())
.then(user=>{
  if(user.id){
    this.props.loadUser(user);
    this.props.userChange("home");
  }
})
}

  render(){
    return(
  <div className="br2 shadow-3 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw5 center">
  <div className='pa4 black-80'>
  <div className="measure">
    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
      <legend className="f2 fw6 ph0 mh0">Register</legend>
       <div className="mt3">
        <label className="db fw6 lh-copy f6" htmlFor="name">Name</label>
        <input 
        onChange={this.onNameChange}
        className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
        type="text" 
        name="name"  
        id="name"/>
      </div>
      <div className="mt3">
        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
        <input
         onChange={this.onEmailChange}
         className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
         type="email" 
         name="email-address"  
         id="email-address"/>
      </div>
      <div className="mv3">
        <label 
         onChange={this.onPasswordChange}  
         className="db fw6 lh-copy f6"
         htmlFor="password">
          Password
        </label>
        <input className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password"/>
      </div>
    </fieldset>
   <div className="">
      <input onClick={this.onRegister} className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" type="submit" value="Register"/>
    </div>
  </div>
    </div>
    </div>
  );
}
  }


export default Register;
